// –ò–º–ø–æ—Ä—Ç —à–∞–±–ª–æ–Ω–∞ –¥–ª—è —Å–ø–∏—Å–∫–∞ —Å—Ç—Ä–∞–Ω
import galleryTpl from '../templates/gallery-item.hbs';
import alert from './notify';
import { galleryRef } from './refs';

// –§—É–Ω–∫—Ü–∏—è, –æ–±–Ω–æ–≤–ª—è—é—â–∞—è –≤—ã–¥–∞—á—É –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–π
function updateGallery(hits) {
  const markup = galleryTpl(hits);

  if (hits.length === 0) {
    alert({
      type: 'notice',
      text: 'Nothing found ‚òπ',
      delay: 2000,
      width: '300px',
      maxTextHeight: null,
    });
  }

  galleryRef.insertAdjacentHTML('beforeend', markup);
}

// –≠–∫—Å–ø–æ—Ä—Ç —Ñ—É–Ω–∫—Ü–∏–∏ –≤–æ –≤–Ω–µ—à–Ω–∏–π –∫–æ–¥
export default updateGallery;
